<template>
  <ul class="nav nav-tabs justify-content-end">
    <li class="nav-item" v-for="sheet in sheets">
      <button 
        class="nav-link" 
        :class="currentSheet === sheets.indexOf(sheet)? 'active' : ''"
        @click.prevent="changeSheet" 
      >{{ sheet.sheet }}</button>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['sheets', 'currentSheet'],
  methods: {
    changeSheet(event){
      console.log(event)
      this.$emit('changeSheet', event)
    }
  }
}
</script>